import React, { Component } from "react";
import foot from "../images/Add.png";
import Facebook from "../images/FB.PNG";
import twitter from "../images/Twitter.PNG";
import youTube from "../images/playIcon.PNG";
import insta from "../images/Instagram.PNG";
import appstore from "../images/AppStore.PNG";
import play from "../images/GooglePlay.PNG";



class Footer extends Component{
    render(){
        return(
            <div>
                <img style={{marginTop:"10%"}} src={foot} width="100%"/>
                <div style={{paddingTop:"20px",backgroundColor:"lightgrey"}} className="container-fluid">
                    <div className="row">
                        <div style={{marginLeft:"25px"}} className="col-2">
                        <p> <b>POPULAR CATEGORIES </b> </p>
                            <ul style={{listStyleType:"none",padding:"0px", fontSize: '0.7em'}}>
                                <li>Cars</li>
                                <li>Flats for Rent</li>
                                <li>Jobs</li>
                                <li>Mobile Phones</li>
                            </ul>
                        </div>
                        <div style={{marginLeft:"25px"}} className="col-2">
                        <p> <b>TRENDING SEARCHES </b> </p>
                            <ul style={{listStyleType:"none",padding:"0px", fontSize: '0.7em'}}>
                                <li>Bikes</li>
                                <li>Watches</li>
                                <li>Books</li>
                                <li>Dogs</li>
                            </ul>
                        </div>
                        <div style={{marginLeft:"25px"}} className="col-2">
                        <p> <b>ABOUT US </b> </p>
                            <ul style={{listStyleType:"none",padding:"0px", fontSize: '0.7em'}}>
                                <li>About OLX group</li>
                                <li>OLX blog</li>
                                <li>Contact us</li>
                                <li>OLX for business</li>
                            </ul>
                        </div>
                        <div style={{marginLeft:"25px"}} className="col-2">
                        <p> <b>OLX </b> </p>
                            <ul style={{listStyleType:"none",padding:"0px", fontSize: '0.7em'}}>
                                <li>Help</li>
                                <li>Sitemap</li>
                                <li>Legal and Privacy informations</li>
                                <li></li>
                            </ul>
                        </div>
                        <div style={{marginLeft:"25px"}} className="col-2">
                        <p> <b> FOLLOW US </b> </p>
                        <ul style={{listStyleType:"none",padding:"0px",display:"inline-flex"}}>
                            <li style={{marginRight:"10%"}}><img src={Facebook}/> </li>
                            <li style={{marginRight:"10%"}}><img src={twitter}/></li>
                            <li style={{marginRight:"10%"}}><img src={youTube}/></li>
                            <li style={{marginRight:"10%"}}><img src={insta}/></li>
                        </ul>
                        <ul style={{listStyleType:"none",padding:"0px",display:"inline-flex"}}>
                            <li style={{marginRight:"10%"}}><img src={appstore}/> </li>
                            <li style={{margin: '10px'}}><img src={play}/></li>
                        </ul>

                        </div>

                    </div>


                </div>

                <div style={{backgroundColor:"darkslategrey",color:"white",display:"inline-flex", width:"100%", height:'50px'}}>
                    <span style={{marginLeft: '50px', fontSize: '0.9em', marginTop: '15px' }}><b> Other Countries </b> India-South Africa-Indonesia</span>
                    <span style={{marginLeft: '620px', fontSize: '0.9em', marginTop: '15px' }}><b> Free Classifieds in Pakistan. </b> © 2006-2020 OLX</span>
                </div>
            </div>
        )
    }
}

export default Footer