import React from "react"
import { Component } from "react";
import { Button } from "react-bootstrap";
import Modal from 'react-bootstrap/Modal';
import sellCard from '../images/sellCard.PNG'

class Sell extends Component {
  render() {
    return (

      <Modal.Dialog>
        <Modal.Header closeButton>
          {/* <Modal.Title>Modal title</Modal.Title> */}
          <img style={{ marginLeft: '60px' }} src={sellCard} alt="" />
        </Modal.Header>

        <Modal.Body>
          <Button style={{width: '460px', marginBottom: '10px'}} variant="outline-dark"><b> Continue with phone </b></Button>
          <Button style={{width: '460px', marginBottom: '10px'}} variant="outline-dark"><img style={{height: '30px'}} src={"https://i.pinimg.com/originals/1b/60/f9/1b60f9d42fd84e31b304d5e7779cccfd.png"} alt=""/><b> Continue with facebook </b></Button>
          <Button style={{width: '460px', marginBottom: '10px'}} variant="outline-dark"><img style={{height: '20px'}} src={"data:image/png;base64,"} alt=""/><b> Continue with google</b></Button>
          <Button style={{width: '460px', marginBottom: '10px'}} variant="outline-dark"><b> Continue with email </b> </Button>
        </Modal.Body>

        <Modal.Footer>

          <p style={{ textAlign: 'center' }}>We won't share your personal details with anyone
          <br />
          If you continue, you are accepting OLX Terms and Conditions and Privacy Policy</p>
        </Modal.Footer>
      </Modal.Dialog>
    )
  }
}

export default Sell