import React from "react"
import logoImage from "../images/Logo.PNG"
import { Navbar, Nav, FormControl, Button, InputGroup, NavDropdown } from 'react-bootstrap';
import SearchIcon from '@material-ui/icons/Search';
import selling from "../images/sellLogo.png"
import top from "../images/img.png"
import { Link } from "react-router-dom";
import TextField from '@material-ui/core/TextField';
import Autocomplete from '@material-ui/lab/Autocomplete';


class Top extends React.Component {
    render() {
        return (
            <div>
                <Navbar bg="light" variant="light">
                    <Navbar.Brand href="#home"><Link to="/"><img src={logoImage} width="70px" /></Link></Navbar.Brand>
                    <Nav className="mr-auto">
                        <Autocomplete
                            id="combo-box-demo"
                            options={[
                                { title: "Pakistan" },
                                { title: "Punjab" },
                                { title: "Islamabad Capital Territory" },
                                { title: "Sindh" },
                                { title: "Khyber Pakhtunkhwa" }
                            ]}
                            getOptionLabel={(option) => option.title}
                            style={{ width: 260 }}
                            renderInput={(params) => <TextField {...params} placeholder="Pakistan" variant="outlined" />}
                        />
                        <InputGroup style={{ marginLeft: "20px", width: "730px" }} className="mb-3" >
                            <FormControl className="form-control-lg"
                                placeholder="Find Cars , Mobile phones and more..."
                                aria-label="Find Cars , Mobile phones and more..."
                                aria-describedby="basic-addon2" />

                            <InputGroup.Append>
                                <Button className="btn btn-dark"><SearchIcon /></Button>
                            </InputGroup.Append>
                        </InputGroup>


                        <Nav.Link style={{ marginLeft: "20px", marginTop: '7px', fontSize: '1.1em' }} href="/Sell"> <u> <b> Login </b> </u> </Nav.Link>
                        <Link to="/Sell"><img width="110px" height="45px" style={{ marginLeft: "8px", marginTop: '7px' }} src={selling} alt="" /></Link>
                    </Nav>

                </Navbar>
                <Navbar bg="white" expand="lg">
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <div style={{ fontSize: '1em', marginLeft: '10px' }}>
                            <Nav className="mr-auto">
                                <NavDropdown title="All Categories" id="basic-nav-dropdown">
                                    <Nav.Link >
                                        <Link to="/MobilePhone">Mobile Phones</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to="/Cars">Cars</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to="/Motorcycles">Motorcycles</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to="/Houses">Houses</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to="/TVAudioVideo">TV-Audio-Video</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to="/Tablets">Tablets</Link>
                                    </Nav.Link>
                                    <Nav.Link>
                                        <Link to="/Landsandplots">Lands and plots</Link>
                                    </Nav.Link>

                                </NavDropdown>

                                <Nav.Link >
                                    <Link to="/MobilePhone">Mobile Phones</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link to="/Cars">Cars</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link to="/Motorcycles">Motorcycles</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link to="/Houses">Houses</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link to="/TVAudioVideo">TV-Audio-Video</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link to="/Tablets">Tablets</Link>
                                </Nav.Link>
                                <Nav.Link>
                                    <Link to="/Landsandplots">Lands and plots</Link>
                                </Nav.Link>
                            </Nav>
                        </div>
                    </Navbar.Collapse>
                </Navbar>
                <img src={top} width="100%" />
            </div>
        )
    }
}



export default Top