import React, { Component } from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route
  } from "react-router-dom";

import Home from "../linkPages/Home"
import MobilePhone from "../linkPages/MobilePhone";
import Sell from "../linkPages/Sell";
import Cars from "../linkPages/Cars";
import Houses from "../linkPages/Houses";
import LandsandPlots from "../linkPages/LandsandPlots";
import Motorcycles from "../linkPages/Motorcycles";
import TVAudioVideo from "../linkPages/TVAudioVideo";
import Tablets from "../linkPages/Tablets";
import Mobile1 from "../MobilePhonesLink/Mobile1";
import Mobile2 from "../MobilePhonesLink/Mobile2";
import Mobile3 from "../MobilePhonesLink/Mobile3";
import House1 from "../houseLink/House1";
import House2 from "../houseLink/House2";
import House3 from "../houseLink/House3";
import Car1 from "../carLink/Car1";
import Car2 from "../carLink/Car2";
import Car3 from "../carLink/Car3";
import MotorCycle1 from '../MotorCyclesLink/MotorCycle1';
import MotorCycle2 from '../MotorCyclesLink/MotorCycle2';
import MotorCycle3 from '../MotorCyclesLink/MotorCycle3';
import TV1 from '../TV-Video-Audio/TV1';
import TV2 from '../TV-Video-Audio/TV2';
import TV3 from '../TV-Video-Audio/TV3';
import Tablet1 from '../Tablets/Tablet1';
import Tablet2 from '../Tablets/Tablet2';
import Tablet3 from '../Tablets/Tablet3';
import Land1 from '../LandPlots_Link/Land1';
import Land2 from '../LandPlots_Link/Land2';
import Land3 from '../LandPlots_Link/Land3';









  class AppRouter extends Component{
      render() {
          return (
            <Router>
              <Switch>
                  <Route exact path="/" component={Home}/>
                  <Route exact path="/MobilePhone" component={MobilePhone}/>
                  <Route exact path="/Sell" component={Sell}/>
                  <Route exact path="/Cars" component={Cars}/>
                  <Route exact path="/Houses" component={Houses}/>
                  <Route exact path="/LandsandPlots" component={LandsandPlots}/>
                  <Route exact path="/Motorcycles" component={Motorcycles}/>
                  <Route exact path="/TVAudioVideo" component={TVAudioVideo}/>
                  <Route exact path="/Tablets" component={Tablets}/>
                  <Route exact path="/Mobile1" component={Mobile1}/>
                  <Route exact path="/Mobile2" component={Mobile2}/>
                  <Route exact path="/Mobile3" component={Mobile3}/>
                  <Route exact path="/House1" component={House1}/>
                  <Route exact path="/House2" component={House2}/>
                  <Route exact path="/House3" component={House3}/>
                  <Route exact path="/Car1" component={Car1}/>
                  <Route exact path="/Car2" component={Car2}/>
                  <Route exact path="/Car3" component={Car3}/>
                  <Route exact path="/MotorCycle1" component={MotorCycle1}/>
                  <Route exact path="/MotorCycle2" component={MotorCycle2}/>
                  <Route exact path="/MotorCycle3" component={MotorCycle3}/>
                  <Route exact path="/TV1" component={TV1}/>
                  <Route exact path="/TV2" component={TV2}/>
                  <Route exact path="/TV3" component={TV3}/>
                  <Route exact path="/Tablet1" component={Tablet1}/>
                  <Route exact path="/Tablet2" component={Tablet2}/>
                  <Route exact path="/Tablet3" component={Tablet3}/>
                  <Route exact path="/Land1" component={Land1}/>
                  <Route exact path="/Land2" component={Land2}/>
                  <Route exact path="/Land3" component={Land3}/>
                
  







                  


              
              </Switch>
              </Router>
          )
      }
  }

  export default AppRouter;